import React from 'react'
import { graphql } from 'gatsby'
import Img from 'gatsby-image'
import styled from 'styled-components'
import Layout from '../components/layout'
import ScrollAnimation from 'react-animate-on-scroll'

const StyledHero = styled.div`
  display: flex;
  flex-direction: row;
  justiy-content: flex-start;
  max-width: 600px;
  margin: 6rem auto 2rem;

  .hero-left {
    width: min-content;
    margin-right: 2rem;
    min-width: 200px;
  }

  .hero-title,
  .hero-client {
    margin: 0;
  }

  .hero-title {
    font-size: 48px;
    span {
      text-transform: uppercase;
    }
  }

  .hero-information {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: flex-start;
    align-items: flex-start;

    em {
      font-size: 0.6rem;
      margin: 0;
      min-width: 50px;
      font-weight: 600;
    }
  }

  .hero-client {
    font-family: serif;
    margin-bottom: 1rem;
  }

  .hero-client,
  .hero-services {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;

    span {
      font-size: 0.8rem;
      text-transform: uppercase;
      font-weight: 100;
    }
  }

  .hero-services {
    ul {
      margin: 0;
    }

    &-list {
      display: flex;
      flex-direction: row;

      & > *:not(:last-child) {
        margin-right: 1rem;
      }

      li {
        line-height: 1;
      }
    }
  }
`

const Wrapper = styled.div`
  padding: 0 2rem;

  p {
    font-weight: 100;
  }

  .content-body {
    max-width: 600px;
    margin: 4rem auto;
  }
`

const StyledList = styled.ul`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  .image {
    padding: 1rem;
  }

  .image-half {
    flex-basis: 50%;
  }

  .image-full {
    flex-basis: 100%;
  }
`

const Project = ({ data, location, pageContext }) => {
  const {
    featuredImage,
    title,
    client,
    images,
    services,
  } = data.markdownRemark.frontmatter
  const { html } = data.markdownRemark

  return (
    <Layout themeColor="white">
      <StyledHero>
        <div className="hero-left">
          <h1 className="hero-title">{title}</h1>
        </div>
        <div className="hero-right">
          <div className="hero-information">
            <div className="hero-client">
              <em>Client:</em>
              <span>{client}</span>
            </div>
            <div className="hero-services">
              <em>Services:</em>
              <ul className="hero-services-list">
                {services &&
                  services.map(item => {
                    return (
                      <li>
                        <span>{item.service}</span>
                      </li>
                    )
                  })}
              </ul>
            </div>
          </div>
        </div>
      </StyledHero>
      <Img fluid={featuredImage.childImageSharp.fluid} />
      <Wrapper>
        <div
          className="content-body"
          dangerouslySetInnerHTML={{ __html: html }}
        ></div>
        <StyledList>
          {images.map(node => {
            const { image, image_alt, half_image } = node
            return (
              <li
                className={half_image ? 'image image-half' : 'image image-full'}
              >
                <ScrollAnimation key={`${image_alt}`} animateIn="fadeInUp">
                  <Img fluid={image.childImageSharp.fluid} alt={image_alt} />
                </ScrollAnimation>
              </li>
            )
          })}
        </StyledList>
      </Wrapper>
    </Layout>
  )
}

export default Project

export const pageQuery = graphql`
  query($slug: String!) {
    markdownRemark(frontmatter: { slug: { eq: $slug } }) {
      frontmatter {
        title
        slug
        client
        services {
          service
        }
        featuredImage {
          childImageSharp {
            fluid(maxWidth: 1480) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
        images {
          image {
            childImageSharp {
              fluid(maxWidth: 1480) {
                ...GatsbyImageSharpFluid_withWebp
              }
            }
          }
          image_alt
          half_image
        }
      }
      html
    }
  }
`
